import React, { FunctionComponent, useCallback } from 'react'

import { BadgeButton } from '../../../components/elements'
import type { QuestionData } from '../../../models'
import { useAdminActions } from '../hooks/useAdminActions'
import { useIsAdmin } from '../hooks/useIsAdmin'

const useConfirm = (fn: Function, message: string) => {
  return useCallback(() => {
    const result = confirm(message)
    if (result) {
      fn()
    }
  }, [fn, message])
}

export const QuestionAdmin: FunctionComponent<QuestionData> = ({
  id,
  published,
  answered,
}) => {
  const isAdmin = useIsAdmin()
  const { remove, publish, setAnswered } = useAdminActions(id!)

  const removeConfirm = useConfirm(remove, ' ?')
  const publishConfirm = useConfirm(publish, ' ?')
  const answeredConfirm = useConfirm(
    setAnswered,
    ' ,  ?'
  )

  if (!isAdmin) {
    return null
  }
  return (
    <>
      <BadgeButton size="xxxs" title="" onClick={removeConfirm}>
        
      </BadgeButton>
      {!published && (
        <BadgeButton size="xxxs" title="" onClick={publishConfirm}>
          
        </BadgeButton>
      )}
      {published && !answered && (
        <BadgeButton
          size="xxxs"
          title="  "
          onClick={answeredConfirm}
        >
          
        </BadgeButton>
      )}
    </>
  )
}
