import React, { FunctionComponent } from 'react'
import { User, ThumbsUp } from 'react-feather'
import { format } from 'date-fns'
import { ru } from 'date-fns/locale'
import styled from 'styled-components'

import { BadgeButton } from '../../../components/elements'
import { Panel, Meta } from '../../../components/layout'
import { QuestionData } from '../../../models'
import { rhythm } from '../../../utils/typography'
import { useIsAdmin } from '../hooks/useIsAdmin'
import { useUpvote } from '../hooks/useUpvote'
import { QuestionAdmin } from '../questionAdmin'

const QuestionContainer = styled.li`
  ${Panel} {
    position: relative;
  }
`

const UnpublishedNotice = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;

  backdrop-filter: blur(4px);
`

const QuestionHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
`
export const QuestionListItem: FunctionComponent<QuestionData> = props => {
  const {
    id,
    userCanVote,
    votes = 0,
    question,
    created,
    author,
    answered,
    published,
    talk,
  } = props
  const upvote = useUpvote()
  const isAdmin = useIsAdmin()

  return (
    <QuestionContainer>
      <Panel>
        {!published && !isAdmin && (
          <UnpublishedNotice>
            <div>   </div>
          </UnpublishedNotice>
        )}

        {!published && isAdmin && (
          <p>
            <strong> !</strong>
          </p>
        )}
        <QuestionHeader>
          <Meta title={author || ''} Icon={User}>
            <p>
              {format(new Date(created || 0), 'd MMMM y, HH:mm', {
                locale: ru,
              })}
              <br />
              <small>
                <em>: {talk}</em>
              </small>
            </p>
          </Meta>
          <div>
            <QuestionAdmin id={id} published={published} answered={answered} />
            <BadgeButton
              size="xxxs"
              active={!userCanVote}
              disabled={!userCanVote}
              onClick={() => upvote(id!)}
            >
              {votes} <ThumbsUp size={rhythm(0.6)} />
            </BadgeButton>
          </div>
        </QuestionHeader>

        <p className="question">{question}</p>
      </Panel>
    </QuestionContainer>
  )
}
